.features {
	position: relative;

	&::after {
		content: "";
		position: absolute;
		left: -50%;
		bottom: 0;
		z-index: 10;
		width: 100%;
		height: 20px;
		background: $color-lemon;
		transform: translateX(percentage( -115 / 1130 ));

		@include pc {
			height: 20 / 1250 * 100vw;
		}
	
		@include tablet {
			height: 20 / 1024 * 100vw;
		}
	
		@include mobile {
			display: none;
		}
	}

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 auto;
		padding: 76px 0 0;
		max-width: 1130px;

		@include pc {
			padding: 76 / 1250 * 100vw 0 0;
			max-width: 1130 / 1250 * 100vw;
		}
	
		@include tablet {
			padding: 76 / 1024 * 100vw 0 0;
			max-width: 100%;
		}
	
		@include mobile {
			flex-direction: column;
			padding: 24 / 360 * 100vmin 0;
		}
	}

	&__media {
		display: flex;
		flex-direction: column;
		border-bottom: 20px solid $color-lemon;
		width: percentage(450 / 1130);

		@include pc {
			border-bottom: 20 / 1250 * 100vw solid $color-lemon;
		}
	
		@include tablet {
			border-bottom: 20 / 1024 * 100vw solid $color-lemon;
			padding-left: 40 / 1024 * 100vw;
		}
	
		@include mobile {
			border-bottom: 20 / 360 * 100vmin solid $color-lemon;
			width: 100%;
		}

		&__header {
			display: flex;
			align-items: center;
			margin-bottom: 59px;
			padding: 15px 0 0 8px;

			@include pc {
				margin-bottom: 59 / 1250 * 100vw;
				padding: 15 / 1250 * 100vw 0 0 8 / 1250 * 100vw;
			}
		
			@include tablet {
				margin-bottom: 59 / 1024 * 100vw;
				padding: 15 / 1024 * 100vw 0 0 8 / 1024 * 100vw;
			}
		
			@include mobile {
				display: flex;
				justify-content: center;
				margin-bottom: 20 / 360 * 100vmin;
				padding: 0;
			}

			&__logo {
				flex-shrink: 0;
				margin-right: 39px;
				width: 91px;
				height: 90px;

				@include pc {
					margin-right: 39 / 1250 * 100vw;
					width: 91 / 1250 * 100vw;
					height: 90 / 1250 * 100vw;
				}
			
				@include tablet {
					margin-right: 39 / 1024 * 100vw;
					width: 91 / 1024 * 100vw;
					height: 90 / 1024 * 100vw;
				}
			
				@include mobile {
					margin-right: 18 / 360 * 100vmin;
					width: 70 / 360 * 100vmin;
					height: 69 / 360 * 100vmin;
				}

				img {
					display: block;
					max-width: 100%;
				}
			}

			&__text {
				margin: 0;
				font-weight: 500;
				font-size: 25px;
				line-height: 26px;
				text-transform: uppercase;
				color: $color-royal-purple-1;

				@include pc {
					font-size: 25 / 1250 * 100vw;
					line-height: 26 / 1250 * 100vw;
				}
			
				@include tablet {
					font-size: 25 / 1024 * 100vw;
					line-height: 26 / 1024 * 100vw;
				}
			
				@include mobile {
					font-size: 18 / 360 * 100vmin;
					line-height: 20 / 360 * 100vmin;
				}
			}
		}

		&__body {
			margin-top: auto;
			padding-right: 32px;

			@include pc {
				padding-right: 32 / 1250 * 100vw;
			}
		
			@include tablet {
				padding-right: 32 / 1024 * 100vw;
			}
		
			@include mobile {
				padding: 0 72 / 360 * 100vmin 0  42 / 360 * 100vmin;
			}

			img {
				display: block;
				margin: 0 auto;
				max-width: 100%;
			}
		}
	}

	&__content {
		width: 51%;
		
		@include mobile {
			width: 100%;
			padding: 20 / 360 * 100vmin;
		}
	}

	&__list {
		list-style: none;
		margin: 0;
		padding: 0;

		&__item {
			position: relative;
			margin-bottom: 24px;
			padding-left: 59px;
			font-weight: 700;
			font-size: 17px;
			line-height: 36px;
			text-transform: uppercase;
			color: $color-black;

			@include pc {
				margin-bottom: 24 / 1250 * 100vw;
				padding-left: 59 / 1250 * 100vw;
				font-size: 17 / 1250 * 100vw;
				line-height: 36 / 1250 * 100vw;
			}
		
			@include tablet {
				margin-bottom: 24 / 1024 * 100vw;
				padding-left: 59 / 1024 * 100vw;
				font-size: 17 / 1024 * 100vw;
				line-height: 36 / 1024 * 100vw;
			}
		
			@include mobile {
				margin-bottom: 18 / 360 * 100vmin;
				padding-left: 59 / 360 * 100vmin;
				font-size: 17 / 360 * 100vmin;
				line-height: 22 / 360 * 100vmin;

				&:first-child,
				&:last-child {
					line-height: 36 / 360 * 100vmin;
				}
			}

			&::before {
				content: "";
				position: absolute;
				left: 0;
				width: 36px;
				height: 36px;
				background: url('../images/checklist.png') 0 0 no-repeat;
				background-size: cover;

				@include pc {
					width: 36 / 1250 * 100vw;
					height: 36 / 1250 * 100vw;
				}
			
				@include tablet {
					width: 36 / 1024 * 100vw;
					height: 36 / 1024 * 100vw;
				}
			
				@include mobile {
					width: 36 / 360 * 100vmin;
					height: 36 / 360 * 100vmin;
				}
			}

			&:last-child {
				margin-bottom: 40px;

				@include pc {
					margin-bottom: 40 / 1250 * 100vw;
				}
			
				@include tablet {
					margin-bottom: 40 / 1024 * 100vw;
				}
			
				@include mobile {
					margin-bottom: 40 / 360 * 100vmin;
				}
			}
		}
	}

	&__action {
		display: flex;
		justify-content: flex-end;
		padding-right: 35px;

		@include pc {
			padding-right: 35 / 1250 * 100vw;
		}
	
		@include tablet {
			padding-right: 35 / 1024 * 100vw;
		}
	
		@include mobile {
			display: flex;
			justify-content: center;
			padding-right: 0;
		}
	}
}