.btn {
	display: block;
	padding: 21px 26px 18px 26px;
	box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.35);
	border-radius: 25px;
	border: none;
	font-weight: 700;
	font-size: 17px;
	line-height: 21px;
	background-color: $color-lemon;
	text-transform: uppercase;
	text-decoration: none;
	color: $color-martinique;
	transition: box-shadow 0.15s, background-color 0.3s, color 0.3s;

	@include pc {
		padding: 21 / 1250 * 100vw 26 / 1250 * 100vw 18 / 1250 * 100vw 26 / 1250 * 100vw;
		box-shadow: 1 / 1250 * 100vw 1 / 1250 * 100vw 0 rgba(0, 0, 0, 0.35);
		border-radius: 25 / 1250 * 100vw;
		font-size: 17 / 1250 * 100vw;
		line-height: 21 / 1250 * 100vw;
	}

	@include tablet {
		padding: 21 / 1024 * 100vw 26 / 1024 * 100vw 18 / 1024 * 100vw 26 / 1024 * 100vw;
		box-shadow: 1 / 1024 * 100vw 1 / 1024 * 100vw 0 rgba(0, 0, 0, 0.35);
		border-radius: 25 / 1024 * 100vw;
		font-size: 17 / 1024 * 100vw;
		line-height: 21 / 1024 * 100vw;
	}

	@include mobile {
		box-shadow: 1 / 360 * 100vmin 1 / 360 * 100vmin 0 rgba(0, 0, 0, 0.35);
		border-radius: 12 / 360 * 100vmin;
		padding: 16 / 360 * 100vmin 20 / 360 * 100vmin 14 / 360 * 100vmin;
		font-size: 14 / 360 * 100vmin;
		line-height: 16 / 360 * 100vmin;
	}

	&:hover {
		box-shadow: 0 0 0 rgba(0, 0, 0, 0.35);
		background-color: $color-martinique;
		color: $color-lemon;
	}
}