.contacts {
	overflow: hidden;

	&__block {
		margin-bottom: 48px;
		padding-left: 0px;

		@include pc {
			margin-bottom: 48 / 1250 * 100vw;
			padding-left: 0 / 1250 * 100vw;
		}
	
		@include tablet {
			margin-bottom: 48 / 1024 * 100vw;
			padding-left: 0 / 1024 * 100vw;
		}
	
		@include mobile {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 0;
			padding: 0;
		}

		&__desc {
			margin-bottom: 8px;
			font-weight: 700;
			font-size: 10px;
			line-height: 12px;
			text-transform: uppercase;
			color: $color-white;

			@include pc {
				margin-bottom: 8 / 1250 * 100vw;
				font-size: 10 / 1250 * 100vw;
				line-height: 12 / 1250 * 100vw;
			}
		
			@include tablet {
				margin-bottom: 8 / 1024 * 100vw;
				font-size: 10 / 1024 * 100vw;
				line-height: 12 / 1024 * 100vw;
			}
		
			@include mobile {
				margin-bottom: 8 / 360 * 100vmin;
				font-size: 10 / 360 * 100vmin;
				line-height: 12 / 360 * 100vmin;
			}
		}

		&__location {
			margin: 0 0 22px;
			font-weight: 700;
			font-size: 29px;
			line-height: 30px;
			text-transform: uppercase;
			color: $color-lemon;

			@include pc {
				margin: 0 0 22 / 1250 * 100vw;
				font-size: 29 / 1250 * 100vw;
				line-height: 30 / 1250 * 100vw;
			}
		
			@include tablet {
				margin: 0 0 22 / 1024 * 100vw;
				font-size: 29 / 1024 * 100vw;
				line-height: 30 / 1024 * 100vw;
			}
		
			@include mobile {
				margin: 0 0 22 / 360 * 100vmin;
				font-size: 29 / 360 * 100vmin;
				line-height: 30 / 360 * 100vmin;
			}
		}

		&__address {
			font-weight: 700;
			font-size: 13px;
			line-height: 13px;
			text-transform: uppercase;
			color: $color-white;

			@include pc {
				font-size: 13 / 1250 * 100vw;
				line-height: 13 / 1250 * 100vw;
			}
		
			@include tablet {
				font-size: 13 / 1024 * 100vw;
				line-height: 13 / 1024 * 100vw;
			}
		
			@include mobile {
				min-width: 140 / 360 * 100vmin;
				font-size: 13 / 360 * 100vmin;
				line-height: 13 / 360 * 100vmin;
			}
		}

		&__worktime {
			max-width: 140px;
			font-weight: 300;
			font-size: 13px;
			line-height: 14px;
			text-transform: uppercase;
			color: $color-white;

			@include pc {
				max-width: 140 / 1250 * 100vw;
				font-size: 13 / 1250 * 100vw;
				line-height: 14 / 1250 * 100vw;
			}
		
			@include tablet {
				max-width: 140 / 1024 * 100vw;
				font-size: 13 / 1024 * 100vw;
				line-height: 14 / 1024 * 100vw;
			}
		
			@include mobile {
				width: 100%;
				max-width: 140 / 360 * 100vmin;
				font-size: 13 / 360 * 100vmin;
				line-height: 14 / 360 * 100vmin;
			}

			&__text {
				margin: 0 0 17px;
				font-weight: 300;

				@include pc {
					margin: 0 0 17 / 1250 * 100vw;
				}
			
				@include tablet {
					margin: 0 0 17 / 1024 * 100vw;
				}
			
				@include mobile {
					margin: 0 0 17 / 360 * 100vmin;
				}
			}

			&__item {
				display: flex;

				&__day {
					margin-right: 6%;
					width: 40%;

					@include pc {
						
					}
				
					@include tablet {
						
					}
				
					@include mobile {
						
					}
				}
			}
		}
	}
}