.hero {
	position: relative;
	background-color: $color-royal-purple-0;
	background-position: center 0;
	background-repeat: no-repeat;
	background-size: cover;

	&::after {
		content: "";
		position: absolute;
		z-index: 0;
		left: 0;
		right: 0;
		bottom: 0;
		height: percentage(457 / 780);
		background-image: linear-gradient(to top, rgba(107,63,152,1) 0%,rgba(107,63,152,1) 14%,rgba(107,63,152,0.7) 49%,rgba(107,63,152,0) 100%);

		@include mobile {
			background: $color-white;
		}
	}

	.container {
		display: flex;
		flex-direction: column;
		padding-bottom: 42px;
		min-height: 100vh;
	
		@include pc {
			padding-bottom: 42 / 1250 * 100vw;
		}
	
		@include tablet {
			padding-bottom: 42 / 1024 * 100vw;
		}
	
		@include mobile {
			padding-bottom: 20 / 360 * 100vmin;
			min-height: 92vh;

			@media (orientation: landscape) {
				min-height: 280 / 360 * 100vw;
			}
		}
	}

	&.is-success {
		min-height: 100vh;

		&::after {
			display: none;
		}

		.container {
			@include mobile {
				padding: 20 / 360 * 100vmin 10 / 360 * 100vmin;
				min-height: 100vh;

				@media (orientation: landscape) {
					min-height: 320 / 360 * 100vw;
				}
			}
		}
	}
	
	&__inner {
		position: relative;
		z-index: 10;
		display: flex;
		align-items: flex-end;
		margin-top: auto;

		@include mobile {
			flex-shrink: 0;
			flex-grow: 1;
			margin: 0;
			min-height: 200 / 360 * 100vmin;
			background: url('../images/hero.jpg') center 0 no-repeat;
			background-size: cover;

			&::after {
				content: "";
				position: absolute;
				z-index: 0;
				left: 0;
				right: 0;
				bottom: 0;
				height: percentage(457 / 780);
				background-image: linear-gradient(to top, rgba(107,63,152,1) 0%,rgba(107,63,152,1) 14%,rgba(107,63,152,0.7) 49%,rgba(107,63,152,0) 100%);
			}
		}

		.is-success & {
			flex-direction: column;
			flex-grow: 1;
			margin: 0;

			@include mobile {
				flex-shrink: 0;
				flex-grow: 1;
				margin: 0;
				min-height: 200 / 360 * 100vmin;
				background: $color-royal-purple-0;

				&::after {
					display: none;
				}
			}
		}
	}

	&__content {
		margin: 0 auto;
		max-width: 72%;
		text-align: center;

		.is-success & {
			margin: auto;
			max-width: 100%;
		}
	
		@include mobile {
			position: relative;
			z-index: 10;
			max-width: 100%;
		}
	}

	&__title {
		margin: 0 0 25px;
		font-weight: 700;
		font-size: 63px;
		line-height: 75px;
		text-shadow: 1px 1px 0 $color-royal-purple-0;
		text-transform: uppercase;
		color: $color-white;

		@include pc {
			margin: 0 0 25 / 1250 * 100vw;
			font-size: 63 / 1250 * 100vw;
			line-height: 75 / 1250 * 100vw;
			text-shadow: 1 / 1250 * 100vw 1 / 1250 * 100vw 0 $color-royal-purple-0;
		}
	
		@include tablet {
			margin: 0 0 25 / 1024 * 100vw;
			font-size: 48 / 1024 * 100vw;
			line-height: 56 / 1024 * 100vw;
			text-shadow: 1 / 1024 * 100vw 1 / 1024 * 100vw 0 $color-royal-purple-0;
		}
	
		@include mobile {
			margin: 0 0 18 / 360 * 100vw;
			font-size: 24 / 360 * 100vw;
			line-height: 32 / 360 * 100vw;
			text-shadow: 1 / 360 * 100vw 1 / 360 * 100vw 0 $color-royal-purple-0;
		}

		.is-success & {
			margin: 55px auto 0;
			font-weight: 500;
			font-size: 36px;
			line-height: 42px;
			text-shadow: 1px 1px 0 $color-royal-purple-0;
			text-transform: none;
			color: $color-white;

			.is-mobile-only {
				display: none;
			}
	
			@include pc {
				margin: 55 / 1250 * 100vw auto 0;
				font-size: 36 / 1250 * 100vw;
				line-height: 42 / 1250 * 100vw;
				text-shadow: 1 / 1250 * 100vw 1 / 1250 * 100vw 0 $color-royal-purple-0;
			}
		
			@include tablet {
				margin: 55 / 1024 * 100vw auto 0;
				font-size: 32 / 1024 * 100vw;
				line-height: 40 / 1024 * 100vw;
				text-shadow: 1 / 1024 * 100vw 1 / 1024 * 100vw 0 $color-royal-purple-0;
			}
		
			@include mobile {
				margin: 58 / 360 * 100vw auto 0;
				font-size: 24 / 360 * 100vw;
				line-height: 32 / 360 * 100vw;
				text-shadow: 1 / 360 * 100vw 1 / 360 * 100vw 0 $color-royal-purple-0;

				.is-mobile-only {
					display: block;
				}
			}
		}
	}

	&__footer {
		display: block;
		margin: 0 auto 25px;

		&__link {
			font-weight: 300;
			font-size: 18px;
			line-height: 24px;
			text-decoration: none;
			text-transform: uppercase;
			color: $color-lemon;
	
			@include pc {
				margin: 0 auto 25 / 1250 * 100vw;
				font-size: 18 / 1250 * 100vw;
				line-height: 24 / 1250 * 100vw;
			}
		
			@include tablet {
				margin: 0 auto 25 / 1024 * 100vw;
				font-size: 16 / 1024 * 100vw;
				line-height: 21 / 1024 * 100vw;
			}
		
			@include mobile {
				margin: 0 auto 18 / 360 * 100vw;
				font-size: 14 / 360 * 100vw;
				line-height: 20 / 360 * 100vw;
			}
		}
	}
}