*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
}

.container {
	margin: 0 auto;
	padding: 0 40px;
	max-width: 1250px;

	@include pc {
		padding: 0 30 / 1250 * 100vw;
	}

	@include tablet {
		padding: 0 20 / 1024 * 100vw;
	}

	@include mobile {
		padding: 0;
	}
}

.rngst_phone_button {
	display: none!important;
}