.logo {
	position: relative;
	display: flex;
	align-items: center;
	text-decoration: none;

	&__img {
		display: block;
		align-self: center;
		flex-shrink: 0;
		margin-right: 18px;
		border-radius: 50%;
		padding: 3px 3px 4px;
		width: 96px;
		height: 96px;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.35);
		background-color: $color-white;

		@include pc {
			margin-right: 18 / 1250 * 100vw;
			padding: 3 / 1250 * 100vw 3 / 1250 * 100vw 4 / 1250 * 100vw;
			width: 96 / 1250 * 100vw;
			height: 96 / 1250 * 100vw;
			box-shadow: 0 0 8 / 1250 * 100vw rgba(0, 0, 0, 0.35);
		}
	
		@include tablet {
			margin-right: 18 / 1024 * 100vw;
			padding: 3 / 1024 * 100vw 3 / 1024 * 100vw 4 / 1024 * 100vw;
			width: 96 / 1024 * 100vw;
			height: 96 / 1024 * 100vw;
			box-shadow: 0 0 8 / 1024 * 100vw rgba(0, 0, 0, 0.35);
		}
	
		@include mobile {
			margin-right: 8 / 360 * 100vmin;
			padding: 3 / 360 * 100vmin 3 / 360 * 100vmin 4 / 360 * 100vmin;
			width: 54 / 360 * 100vmin;
			height: 54 / 360 * 100vmin;
			box-shadow: 0 0 8 / 360 * 100vmin rgba(0, 0, 0, 0.35);
		}

		img {
			max-width: 100%;
		}
	}

	&__desc {
		display: block;
		margin-bottom: 5px;
		font-size: 17px;
		letter-spacing: 4.5px;
		line-height: 17px;
		text-transform: uppercase;
		color: $color-white;

		@include pc {
			margin-bottom: 5 / 1250 * 100vw;
			font-size: 17 / 1250 * 100vw;
			letter-spacing: 4.5 / 1250 * 100vw;
			line-height: 17 / 1250 * 100vw;
		}
	
		@include tablet {
			margin-bottom: 5 / 1024 * 100vw;
			font-size: 17 / 1024 * 100vw;
			letter-spacing: 4.5 / 1024 * 100vw;
			line-height: 17 / 1024 * 100vw;
		}
	
		@include mobile {
			margin-bottom: 2 / 360 * 100vmin;
			font-size: 8 / 360 * 100vmin;
			letter-spacing: 1 / 360 * 100vmin;
			line-height: 8 / 360 * 100vmin;
		}
	}

	&__name {
		display: block;
		font-weight: 700;
		font-size: 27px;
		line-height: 20px;
		text-transform: uppercase;
		color: $color-white;

		@include pc {
			font-size: 27 / 1250 * 100vw;
			line-height: 20 / 1250 * 100vw;
		}
	
		@include tablet {
			font-size: 27 / 1024 * 100vw;
			line-height: 20 / 1024 * 100vw;
		}
	
		@include mobile {
			font-size: 12 / 360 * 100vmin;
			line-height: 16 / 360 * 100vmin;
		}
	}

	&__content {
		@include mobile {
			padding-top: 24 / 360 * 100vmin;
		}
	}

	&__location {
		position: absolute;
		right: 0;
		top: 5px;
		border-radius: 12px;
		padding: 5px 12px 4px;
		background-color: $color-lemon;
		font-weight: 700;
		font-size: 14px;
		line-height: 14px;
		text-transform: uppercase;
		color: $color-royal-purple-0;
		transform: translateX(100%);

		@include pc {
			top: 5 / 1250 * 100vw;
			border-radius: 12 / 1250 * 100vw;
			padding: 5 / 1250 * 100vw 12 / 1250 * 100vw 4 / 1250 * 100vw;
			font-size: 14 / 1250 * 100vw;
			line-height: 14 / 1250 * 100vw;
		}
	
		@include tablet {
			top: 5 / 1024 * 100vw;
			border-radius: 12 / 1024 * 100vw;
			padding: 5 / 1024 * 100vw 12 / 1024 * 100vw 4 / 1024 * 100vw;
			font-size: 14 / 1024 * 100vw;
			line-height: 14 / 1024 * 100vw;
		}
	
		@include mobile {
			left: 60 / 360 * 100vmin;
			right: auto;
			top: 6 / 360 * 100vmin;
			border-radius: 12 / 360 * 100vmin;
			padding: 3 / 360 * 100vmin 6 / 360 * 100vmin 2 / 360 * 100vmin;
			font-size: 10 / 360 * 100vmin;
			line-height: 10 / 360 * 100vmin;
			transform: none;
		}
	}
}