.msg {
	opacity: 0;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: -999;
	display: flex;
	align-items: center;
	width: 100%;
	height: 100%;
	background: rgba($color-black, 0.75);

	&__content {
		margin: 0 auto;
		border-radius: 22px;
		padding: 20px;
		max-width: 360px;
		background: $color-white;

		&__text {
			font-size: 18px;
			line-height: 24px;
			text-align: center;
			color: $color-black;
		}
	}

	&.is-active {
		opacity: 1;
		z-index: 999;
	}
}