.header {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	z-index: 100;
	width: 100%;

	&__inner {
		display: flex;
		justify-content: space-between;
		margin: 0 auto;
		padding: 28px 43px 0;
		max-width: 1600px;

		@include pc {
			padding: 28 / 1250 * 100vw 43 / 1250 * 100vw 0;
		}
	
		@include tablet {
			padding: 28 / 1024 * 100vw 43 / 1024 * 100vw 0;
		}
	
		@include mobile {
			padding: 16 / 360 * 100vmin 12 / 360 * 100vmin 0;
		}
	}

	&__logo {
		@include mobile {
			margin-right: auto;
		}
	}

	&__callback {
		flex-shrink: 0;
		padding-top: 15px;

		@include pc {
			padding-top: 15 / 1250 * 100vw;
		}
	
		@include tablet {
			padding-top: 15 / 1024 * 100vw;
		}
	
		@include mobile {
			padding-top: 5 / 360 * 100vmin;
		}
	}
}