.manual {
	&__header {
		margin: 0;
		padding: 77px 0;
		font-weight: 700;
		font-size: 50px;
		font-style: italic;
		line-height: 50px;
		text-transform: uppercase;
		text-align: center;
		background: $color-royal-purple-0;
		color: $color-white;

		@include pc {
			padding: 77 / 1250 * 100vw 0;
			font-size: 50 / 1250 * 100vw;
			line-height: 50 / 1250 * 100vw;
		}
	
		@include tablet {
			padding: 77 / 1024 * 100vw 0;
			font-size: 50 / 1024 * 100vw;
			line-height: 50 / 1024 * 100vw;
		}
	
		@include mobile {
			padding: 42 / 360 * 100vmin 0;
			font-size: 42 / 360 * 100vmin;
			line-height: 42 / 360 * 100vmin;
		}
	}

	&__body {
		margin: 0 auto;
		max-width: 1130px;

		@include pc {
			max-width: 1130 / 1250 * 100vw;
		}
	
		@include tablet {
			max-width: 100%;
		}
	}

	&__step {
		display: flex;
		align-items: center;
		
		@include mobile {
			flex-direction: column;
		}

		&:last-child {
			margin-bottom: 40px;

			@include pc {
				margin-bottom: 40 / 1250 * 100vw;
			}
		
			@include tablet {
				margin-bottom: 40 / 1024 * 100vw;
			}
		
			@include mobile {
				margin-bottom: 40 / 360 * 100vmin;
			}
		}

		&__number {
			position: relative;
			left: -6px;
			z-index: 10;
			margin-top: -14px;
			padding: 32px 24px 28px 34px;
			max-width: 206px;
			font-weight: 100;
			font-size: 50px;
			line-height: 50px;
			text-transform: uppercase;
			text-align: center;
			background-color: $color-gallery;
			color: $color-royal-purple-0;
			transition: background-color 0.3s;

			@include pc {
				left: -6 / 1250 * 100vw;
				margin-top: -14 / 1250 * 100vw;
				padding: 32 / 1250 * 100vw 24 / 1250 * 100vw 28 / 1250 * 100vw 34 / 1250 * 100vw;
				max-width: 206 / 1250 * 100vw;
				font-size: 50 / 1250 * 100vw;
				line-height: 50 / 1250 * 100vw;
			}
		
			@include tablet {
				left: -6 / 1024 * 100vw;
				margin-top: -14 / 1024 * 100vw;
				padding: 32 / 1024 * 100vw 24 / 1024 * 100vw 28 / 1024 * 100vw 34 / 1024 * 100vw;
				max-width: 206 / 1024 * 100vw;
				font-size: 50 / 1024 * 100vw;
				line-height: 50 / 1024 * 100vw;
			}
		
			@include mobile {
				left: -6 / 360 * 100vmin;
				margin: -14 / 360 * 100vmin auto 0;
				padding: 32 / 360 * 100vmin 24 / 360 * 100vmin 28 / 360 * 100vmin 34 / 360 * 100vmin;
				max-width: 206 / 360 * 100vmin;
				font-size: 50 / 360 * 100vmin;
				line-height: 50 / 360 * 100vmin;
			}

			&.is-active {
				background-color: $color-lemon;
			}

			&.is-alt {
				margin: 0 0 -14px 75px;

				@include pc {
					margin: 0 0 -14 / 1250 * 100vw 75 / 1250 * 100vw;
				}
			
				@include tablet {
					margin: 0 0 -14 / 1024 * 100vw 75 / 1024 * 100vw;
				}
			
				@include mobile {
					margin: 60 / 360 * 100vmin auto -14 / 360 * 100vmin;
				}
			}
		}

		&__desc {
			width: 55%;
			padding-left: 75px;

			@include pc {
				padding-left: 75 / 1250 * 100vw;
			}
		
			@include tablet {
				padding-left: 75 / 1024 * 100vw;
			}
		
			@include mobile {
				padding-left: 20 / 360 * 100vmin;
				width: 100%;
			}

			&__title {
				margin: 60px 0 49px;
				font-weight: 700;
				font-size: 50px;
				line-height: 54px;
				text-transform: uppercase;
				color: $color-royal-purple-0;

				@include pc {
					margin: 60 / 1250 * 100vw 0 49 / 1250 * 100vw;
					font-size: 50 / 1250 * 100vw;
					line-height: 54 / 1250 * 100vw;
				}
			
				@include tablet {
					margin: 60 / 1024 * 100vw 0 49 / 1024 * 100vw;
					font-size: 50 / 1024 * 100vw;
					line-height: 54 / 1024 * 100vw;
				}
			
				@include mobile {
					margin: 40 / 360 * 100vmin 0 24 / 360 * 100vmin;
					font-size: 42 / 360 * 100vmin;
					line-height: 46 / 360 * 100vmin;
				}
				
				span {
					display: block;

					&.is-subtitle {
						font-size: 25px;
						line-height: 23px;

						@include pc {
							font-size: 25 / 1250 * 100vw;
							line-height: 23 / 1250 * 100vw;
						}
					
						@include tablet {
							font-size: 25 / 1024 * 100vw;
							line-height: 23 / 1024 * 100vw;
						}
					
						@include mobile {
							font-size: 20 / 360 * 100vmin;
							line-height: 20 / 360 * 100vmin;
						}
					}

					&.is-informer {
						font-weight: 700;
						font-size: 13px;
						line-height: 21px;
						text-transform: uppercase;
						color: rgba($color-black, 0.3);

						@include pc {
							font-size: 13 / 1250 * 100vw;
							line-height: 21 / 1250 * 100vw;
						}
					
						@include tablet {
							font-size: 13 / 1024 * 100vw;
							line-height: 21 / 1024 * 100vw;
						}
					
						@include mobile {
							font-size: 10 / 360 * 100vmin;
							line-height: 16 / 360 * 100vmin;
						}
					}
				}
			}

			&__list {
				list-style: none;
				margin: 0;
				padding: 0;

				&__item {
					position: relative;
					margin-bottom: 28px;
					padding: 2px 0 0 29px;
					font-size: 21px;
					line-height: 26px;
					color: $color-black;

					@include pc {
						margin-bottom: 28 / 1250 * 100vw;
						padding: 2 / 1250 * 100vw 0 0 29 / 1250 * 100vw;
						font-size: 21 / 1250 * 100vw;
						line-height: 26 / 1250 * 100vw;
					}
				
					@include tablet {
						margin-bottom: 28 / 1024 * 100vw;
						padding: 2 / 1024 * 100vw 0 0 29 / 1024 * 100vw;
						font-size: 21 / 1024 * 100vw;
						line-height: 26 / 1024 * 100vw;
					}
				
					@include mobile {
						margin-bottom: 20 / 360 * 100vmin;
						padding: 2 / 360 * 100vmin 0 0 29 / 360 * 100vmin;
						font-size: 18 / 360 * 100vmin;
						line-height: 21 / 360 * 100vmin;
					}

					&::before {
						content: "";
						position: absolute;
						left: 0;
						top: -3px;
						width: 7px;
						height: 100%;
						background-color: $color-lemon;

						@include pc {
							top: -3 / 1250 * 100vw;
							width: 7 / 1250 * 100vw;
						}
					
						@include tablet {
							top: -3 / 1024 * 100vw;
							width: 7 / 1024 * 100vw;
						}
					
						@include mobile {
							top: -3 / 360 * 100vmin;
							width: 7 / 360 * 100vmin;
						}
					}

					&::after {
						content: "";
						position: absolute;
						left: 7px;
						top: -3px;
						width: 3px;
						height: 100%;
						background-color: $color-royal-purple-0;

						@include pc {
							left: 7 / 1250 * 100vw;
							top: -3 / 1250 * 100vw;
							width: 3 / 1250 * 100vw;
						}
					
						@include tablet {
							left: 7 / 1024 * 100vw;
							top: -3 / 1024 * 100vw;
							width: 3 / 1024 * 100vw;
						}
					
						@include mobile {
							left: 7 / 360 * 100vmin;
							top: -3 / 360 * 100vmin;
							width: 3 / 360 * 100vmin;
						}
					}
					
					span {
						display: block;
						font-size: 13px;
						line-height: 24px;
						font-weight: 700;
						text-transform: uppercase;
						color: rgba($color-black, 0.5);

						@include pc {
							font-size: 13 / 1250 * 100vw;
							line-height: 24 / 1250 * 100vw;
						}
					
						@include tablet {
							font-size: 13 / 1024 * 100vw;
							line-height: 24 / 1024 * 100vw;
						}
					
						@include mobile {
							font-size: 13 / 360 * 100vmin;
							line-height: 24 / 360 * 100vmin;
						}
					}
				}
			}

			&__text {
				padding-right: 20px;
				font-weight: 700;
				font-size: 13px;
				line-height: 13px;
				text-transform: uppercase;
				color: rgba($color-black, 0.5);

				@include pc {
					padding-right: 20 / 1250 * 100vw;
					font-size: 13 / 1250 * 100vw;
					line-height: 13 / 1250 * 100vw;
				}
			
				@include tablet {
					padding-right: 20 / 1024 * 100vw;
					font-size: 13 / 1024 * 100vw;
					line-height: 13 / 1024 * 100vw;
				}
			
				@include mobile {
					padding-right: 20 / 360 * 100vmin;
					font-size: 10 / 360 * 100vmin;
					line-height: 13 / 360 * 100vmin;
				}

				span {
					display: block;
				}
			}
		}

		&__media {
			position: relative;
			display: flex;
			flex-direction: column;
			width: 45%;

			@include pc {
				
			}
		
			@include tablet {
				
			}
		
			@include mobile {
				width: 100%;
			}

			&__video {
				iframe {
					width: 100%;
				}
			}

			img {
				display: block;
				max-width: 100%;
			}

			&__brand {
				position: absolute;
				left: 0;
				top: 40px;
				width: 150px;

				@include pc {
					top: 40 / 1250 * 100vw;
					width: 150 / 1250 * 100vw;
				}
			
				@include tablet {
					top: 40 / 1024 * 100vw;
					width: 150 / 1024 * 100vw;
				}
			
				@include mobile {
					left: 20 / 360 * 100vmin;
					top: 40 / 360 * 100vmin;
					width: 80 / 360 * 100vmin;
				}
			}

			&__callback {
				display: flex;
				flex-direction: column;
				margin-left: auto;
				margin-right: 32px;
				padding-top: 40px;

				@include pc {
					margin-right: 32 / 1250 * 100vw;
					padding-top: 40 / 1250 * 100vw;
				}
			
				@include tablet {
					margin-right: 32 / 1024 * 100vw;
					padding-top: 40 / 1024 * 100vw;
				}
			
				@include mobile {
					margin: 0 auto;
					padding-top: 40 / 360 * 100vmin;
				}

				&__doctor {
					overflow: hidden;
					margin: 0 auto 30px;
					width: 259px;
					height: 259px;

					@include pc {
						margin: 0 auto 30 / 1250 * 100vw;
						width: 259 / 1250 * 100vw;
						height: 259 / 1250 * 100vw;
					}
				
					@include tablet {
						margin: 0 auto 30 / 1024 * 100vw;
						width: 259 / 1024 * 100vw;
						height: 259 / 1024 * 100vw;
					}
				
					@include mobile {
						margin: 0 auto 30 / 360 * 100vmin;
						width: 259 / 360 * 100vmin;
						height: 259 / 360 * 100vmin;
					}
				}
			}
		}
	}
}