.order {
	&__text {
		padding: 23px 0 19px;
		font-weight: 700;
		font-size: 29px;
		background: $color-white;
		text-transform: uppercase;
		text-align: center;
		color: $color-royal-purple-0;

		@include pc {
			padding: 23 / 1250 * 100vw 40 / 1250 * 100vw 19 / 1250 * 100vw;
			font-size: 29 / 1250 * 100vw;
		}
	
		@include tablet {
			padding: 23 / 1024 * 100vw 40 / 1024 * 100vw 19 / 1024 * 100vw;
			font-size: 29 / 1024 * 100vw;
		}
	
		@include mobile {
			padding: 23 / 360 * 100vmin 40 / 360 * 100vmin 19 / 360 * 100vmin;
			font-size: 29 / 360 * 100vmin;
		}

		span {
			position: relative;
			display: inline-block;
			border-radius: 22px;
			padding: 8px 6px 4px 7px;
			background-color: $color-lemon;
			line-height: 26px;
			vertical-align: baseline;
			color: $color-royal-purple-0;

			@include pc {
				border-radius: 22 / 1250 * 100vw;
				padding: 8 / 1250 * 100vw 6 / 1250 * 100vw 4 / 1250 * 100vw 7 / 1250 * 100vw;
				line-height: 26 / 1250 * 100vw;
			}
		
			@include tablet {
				border-radius: 22 / 1024 * 100vw;
				padding: 8 / 1024 * 100vw 6 / 1024 * 100vw 4 / 1024 * 100vw 7 / 1024 * 100vw;
				line-height: 26 / 1024 * 100vw;
			}
		
			@include mobile {
				border-radius: 22 / 360 * 100vmin;
				padding: 8 / 360 * 100vmin 6 / 360 * 100vmin 4 / 360 * 100vmin 7 / 360 * 100vmin;
				line-height: 26 / 360 * 100vmin;
			}
		}

		&.is-white {
			padding-top: 53px;
			background-color: $color-royal-purple-0;
			color: $color-white;

			@include pc {
				padding-top: 53 / 1250 * 100vw;
			}
		
			@include tablet {
				padding-top: 53 / 1024 * 100vw;
			}
		
			@include mobile {
				padding-top: 53 / 360 * 100vmin;
			}
		}
	}

	&__form {
		padding: 5px 0 49px;
		background-color: $color-royal-purple-0;

		@include pc {
			padding: 5 / 1250 * 100vw 40 / 1250 * 100vw 49 / 1250 * 100vw;
		}
	
		@include tablet {
			padding: 5 / 1024 * 100vw 40 / 1024 * 100vw 49 / 1024 * 100vw;
		}
	
		@include mobile {
			padding: 5 / 360 * 100vmin 20 / 360 * 100vmin 28 / 360 * 100vmin;
		}
	}

	&__location {
		margin: 0;
		padding-bottom: 33px;
		font-weight: 300;
		font-size: 29px;
		text-transform: uppercase;
		text-align: center;
		background-color: $color-royal-purple-0;
		color: $color-white;

		@include pc {
			padding-bottom: 33 / 1250 * 100vw;
			font-size: 29 / 1250 * 100vw;
		}
	
		@include tablet {
			padding-bottom: 33 / 1024 * 100vw;
			font-size: 29 / 1024 * 100vw;
		}
	
		@include mobile {
			padding-bottom: 33 / 360 * 100vmin;
			font-size: 29 / 360 * 100vmin;
		}
	}

	&.is-hidden {
		opacity: 0;
		overflow: hidden;
		position: relative;
		z-index: -100;
		height: 0;
	}
}