.slider {
	position: relative;

	&__inner {
		position: relative;
		margin: 0 auto;

		@include tablet {
			max-width: 80%;
			overflow: hidden;
		}
	}


	&__controls {
		position: absolute;
		left: 0;
		right: 0;
		top: 50%;
		z-index: 1;
		margin: 0 auto;
		height: 0;
		width: 100%;
		max-width: 1064 / 1800 * 100vw;
		transform: translateY(-50%) translateY(-70 / 1800 * 100vw);

		@include pc {
			max-width: 1064 / 1250 * 100vw;
			transform: translateY(-50%) translateY(-70 / 1250 * 100vw);
		}

		@include tablet {
			max-width: 1064 / 1024 * 100vw;
			transform: translateY(-50%) translateY(-70 / 1024 * 100vw);
		}

		@include mobile {
			left: 0;
			right: 0;
			max-width: 100%;
		}

		&__next,
		&__prev {
			display: flex;
			align-items: center;
			justify-content: center;
			float: left;
			width: 27 / 1800 * 100vw;
			height: 18 / 1800 * 100vw;
			opacity: 1;
			transition: opacity 0.3s;
			will-change: opacity;
			cursor: pointer;

			@include pc {
				width: 27 / 1250 * 100vw;
				height: 18 / 1250 * 100vw;
			}
	
			@include tablet {
				width: 27 / 1024 * 100vw;
				height: 18 / 1024 * 100vw;
			}
	
			@include tablet {
				width: 27 / 360 * 100vmin;
				height: 18 / 360 * 100vmin;
			}

			img {
				display: block;
				width: 100%;
			}

			&:hover {
				opacity: 0.6;
			}

			&.slick-disabled {
				opacity: 0.3;
			}
		}

		&__next {
			float: right;
		}
	}

	&__paginations {
		position: relative;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center;
		padding: 15 / 1800 * 100vw 0 10 / 1800 * 100vw;

		@include pc {
			padding: 15 / 1250 * 100vw 0 10 / 1250 * 100vw;
		}

		@include tablet {
			padding: 15 / 1024 * 100vw 0 10 / 1024 * 100vw;
		}

		@include mobile {
			padding: 0;
		}

		.slick-dots {
			display: flex;
			list-style: none;
			margin: 0;
			padding: 0;

			li {
				margin: 0 7 / 1800 * 100vw 0 0;
				font-size: 0;
				line-height: 0;

				@include pc {
					margin: 0 7 / 1250 * 100vw 0 0;
				}
		
				@include tablet {
					margin: 0 7 / 1024 * 100vw 0 0;
				}
		
				@include mobile {
					margin: 0 7 / 360 * 100vmin 0 0;
				}

				&:last-child {
					margin: 0;
				}
			}
		}

		.slick-active {
			button {
				&::after {
					background: rgba($color-white, 0.5);
				}
			}
		}

		button {
			overflow: hidden;
			position: relative;
			outline: none;
			border: none;
			padding: 0;
			width: 33 / 1800 * 100vw;
			height: 65 / 1800 * 100vw;
			text-indent: -9999 / 1800 * 100vw;
			background: transparent;
			transition: border-color 0.3s;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				right: 0;
				top: 50%;
				width: 100%;
				height: 1 / 1800 * 100vw;
				background: rgba($color-white, 0.1);
				transition: background 0.3s;

				@include pc {
					height: 1 / 1250 * 100vw;
				}

				@include tablet {
					height: 1 / 1024 * 100vw;
				}

				@include mobile {
					height: 1 / 360 * 100vmin;
				}
			}

			@include pc {
				width: 33 / 1250 * 100vw;
				height: 65 / 1250 * 100vw;
				text-indent: -9999 / 1250 * 100vw;
			}
	
			@include tablet {
				width: 33 / 1024 * 100vw;
				height: 65 / 1024 * 100vw;
				text-indent: -9999 / 1024 * 100vw;
			}
	
			@include mobile {
				width: 33 / 360 * 100vmin;
				height: 65 / 360 * 100vmin;
				text-indent: -9999 / 360 * 100vmin;
			}
		}
	}
}