.footer {
	position: relative;
	padding: 50px 0;
	background-color: $color-royal-purple-0;

	@include pc {
		padding: 50 / 1250 * 100vw 0;
	}

	@include tablet {
		padding: 50 / 1024 * 100vw 0;
	}

	@include mobile {
		padding: 50 / 360 * 100vmin 0;
	}

	&::before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($color-black, 0.1);
	}

	&__content,
	&__action {
		position: relative;
		z-index: 10;
	}

	&__action {
		display: flex;
		justify-content: center;
	}
}