$sprites: (
	"phone": (
		normal: (
			x: 0,
			y: 0,
			width: 27,
			height: 27,
			image: "sprites.png",
			total_width: 27,
			total_height: 27,
		),
		retina: (
			x: 0,
			y: 0,
			width: 54,
			height: 54,
			image: "sprites@2x.png",
			total_width: 54,
			total_height: 54,
		),
	),
);

@each $name in map-keys($sprites) {
	.sprite-#{$name} {
		@include sprite($name);
	}
}
