.comments {
	padding: 42px 0 22px;

	@include pc {
		padding: 42 / 1250 * 100vw 0 22 / 1250 * 100vw;
	}

	@include tablet {
		padding: 42 / 1024 * 100vw 0 22 / 1024 * 100vw;
	}

	@include mobile {
		padding: 42 / 360 * 100vmin 0 22 / 360 * 100vmin;
	}

	&__header {
		margin: 0 0 29px;
		font-weight: 700;
		font-size: 29px;
		line-height: 29px;
		text-transform: uppercase;
		text-align: center;
		color: $color-royal-purple-0;

		@include pc {
			margin: 0 0 29 / 1250 * 100vw;
			font-size: 29 / 1250 * 100vw;
			line-height: 29 / 1250 * 100vw;
		}
	
		@include tablet {
			margin: 0 0 29 / 1024 * 100vw;
			font-size: 29 / 1024 * 100vw;
			line-height: 29 / 1024 * 100vw;
		}
	
		@include mobile {
			margin: 0 0 20 / 360 * 100vmin;
			padding: 0 20 / 360 * 100vmin;
			font-size: 24 / 360 * 100vmin;
			line-height: 24 / 360 * 100vmin;
		}
	}

	&__item {
		border-bottom: 1px solid rgba($color-black, 0.2);

		@include pc {
			border-bottom: 1 / 1250 * 100vw solid rgba($color-black, 0.2);
		}
	
		@include tablet {
			border-bottom: 1 / 1024 * 100vw solid rgba($color-black, 0.2);
		}
	
		@include mobile {
			border-bottom: 1 / 360 * 100vmin solid rgba($color-black, 0.2);
		}

		&:last-child {
			border: none;

			.comments__item__inner {
				padding-bottom: 33px;

				@include pc {
					padding-bottom: 33 / 1250 * 100vw;
				}
			
				@include tablet {
					padding-bottom: 33 / 1024 * 100vw;
				}
			
				@include mobile {
					padding-bottom: 33 / 360 * 100vmin;
				}
			}
		}

		&__inner {
			margin: 0 auto;
			padding: 39px 0 61px 24px;
			max-width: 1130px;
			min-height: 125px;

			@include pc {
				padding: 39 / 1250 * 100vw 0 61 / 1250 * 100vw 24 / 1250 * 100vw;
				max-width: 1130 / 1250 * 100vw;
				min-height: 125 / 1250 * 100vw;
			}
		
			@include tablet {
				padding: 39 / 1024 * 100vw 0 61 / 1024 * 100vw 24 / 1024 * 100vw;
				max-width: 1130 / 1024 * 100vw;
				min-height: 125 / 1024 * 100vw;
			}
		
			@include mobile {
				padding: 30 / 360 * 100vmin 20 / 360 * 100vmin 50 / 360 * 100vmin;
				max-width: 1130 / 360 * 100vmin;
				min-height: 125 / 360 * 100vmin;
			}
		}

		&__autor {
			display: flex;
			align-items: center;
			margin-bottom: 6px;

			@include pc {
				margin-bottom: 6 / 1250 * 100vw;
			}
		
			@include tablet {
				margin-bottom: 6 / 1024 * 100vw;
			}
		
			@include mobile {
				margin-bottom: 6 / 360 * 100vmin;
			}

			&__avatar {
				flex-shrink: 0;
				margin-right: 22px;
				width: 47px;
				height: 47px;
				background-position: 0 0;
				background-repeat: no-repeat;
				background-size: cover;

				@include pc {
					margin-right: 22 / 1250 * 100vw;
					width: 47 / 1250 * 100vw;
					height: 47 / 1250 * 100vw;
				}
			
				@include tablet {
					margin-right: 22 / 1024 * 100vw;
					width: 47 / 1024 * 100vw;
					height: 47 / 1024 * 100vw;
				}
			
				@include mobile {
					margin-right: 12 / 360 * 100vmin;
					width: 47 / 360 * 100vmin;
					height: 47 / 360 * 100vmin;
				}
			}

			&__details {
				margin: 0;
				font-weight: 700;
				font-size: 17px;
				line-height: 21px;
				text-transform: uppercase;
				color: $color-royal-purple-0;

				@include pc {
					font-size: 17 / 1250 * 100vw;
					line-height: 21 / 1250 * 100vw;
				}
			
				@include tablet {
					font-size: 17 / 1024 * 100vw;
					line-height: 21 / 1024 * 100vw;
				}
			
				@include mobile {
					font-size: 15 / 360 * 100vmin;
					line-height: 21 / 360 * 100vmin;
				}
			}
		}

		&__msg {
			margin: 0;
			padding-left: 69px;
			font-size: 17px;
			line-height: 20px;
			color: $color-royal-purple-0;

			@include pc {
				padding-left: 69 / 1250 * 100vw;
				font-size: 17 / 1250 * 100vw;
				line-height: 20 / 1250 * 100vw;
			}
		
			@include tablet {
				padding-left: 69 / 1024 * 100vw;
				font-size: 17 / 1024 * 100vw;
				line-height: 20 / 1024 * 100vw;
			}
		
			@include mobile {
				padding-left: 59 / 360 * 100vmin;
				font-size: 15 / 360 * 100vmin;
				line-height: 20 / 360 * 100vmin;
			}
		}
	}
}