.form {
	position: relative;
	z-index: 10;
	display: flex;
	flex-direction: column;
	margin: 5px auto 0;
	width: 100%;
	max-width: 1120px;

	@include pc {
		margin: 5 / 1250 * 100vw auto 0;
		max-width: 1120 / 1250 * 100vw;
	}

	@include tablet {
		margin: 5 / 1024 * 100vw auto 0;
		max-width: 1120 / 1024 * 100vw;
	}

	@include mobile {
		margin: 5 / 360 * 100vmin auto 0;
		max-width: 1120 / 360 * 100vmin;
	}

	&__desc {
		display: flex;
		justify-content: space-between;
		border-radius: 25px 25px 0 0;
		margin: 0 auto;
		padding: 17px 20px 15px;
		width: 100%;
		max-width: 90.5%;
		background-color: $color-royal-purple-0;
		font-weight: 400;
		font-size: 13px;
		line-height: 14px;
		text-transform: uppercase;
		color: $color-white;

		@include pc {
			border-radius: 25 / 1250 * 100vw 25 / 1250 * 100vw 0 0;
			padding: 17 / 1250 * 100vw 20 / 1250 * 100vw 15 / 1250 * 100vw;
			font-size: 13 / 1250 * 100vw;
			line-height: 14 / 1250 * 100vw;
		}
	
		@include tablet {
			padding: 17 / 1024 * 100vw 20 / 1024 * 100vw 15 / 1024 * 100vw;
			max-width: 480 / 1024 * 100vw;
			font-size: 13 / 1024 * 100vw;
			line-height: 18 / 1024 * 100vw;
			background: transparent;
			text-align: center;
		}
	
		@include mobile {
			min-width: 100%;
			padding: 8 / 360 * 100vmin 0 / 360 * 100vmin 10 / 360 * 100vmin;
			font-size: 8 / 360 * 100vmin;
			line-height: 12 / 360 * 100vmin;
		}

		&__text {
			@include tablet {
				margin: 0 auto;
			}
		}

		&__contacts {
			font-weight: 100;

			@include tablet {
				display: none;
			}
		}
	}

	&__inner {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		padding: 12px 30px 14px 43px;
		border-radius: 25px;
		background-color: $color-white;

		@include pc {
			padding: 12 / 1250 * 100vw 30 / 1250 * 100vw 14 / 1250 * 100vw 43 / 1250 * 100vw;
			border-radius: 25 / 1250 * 100vw;
		}
	
		@include tablet {
			flex-direction: column;
			margin: 0 auto;
			border-radius: 25 / 1024 * 100vw;
			padding: 12 / 1024 * 100vw 30 / 1024 * 100vw 14 / 1024 * 100vw 43 / 1024 * 100vw;
			max-width: 480 / 1024 * 100vw;
		}
	
		@include mobile {
			border-radius: 12 / 360 * 100vmin;
			padding: 12 / 360 * 100vmin 20 / 360 * 100vmin 14 / 360 * 100vmin 20 / 360 * 100vmin;
			max-width: 100%;
		}
	}

	&__field {
		position: relative;
		width: 260px;
		padding-bottom: 7px;

		@include pc {
			width: 260 / 1250 * 100vw;
			padding-bottom: 7 / 1250 * 100vw;
		}
	
		@include tablet {
			margin-bottom: 12 / 1024 * 100vw;
			padding-bottom: 7 / 1024 * 100vw;
			width: 100%;
		}
		
		@include mobile {
			margin-bottom: 12 / 360 * 100vmin;
			padding-bottom: 7 / 360 * 100vmin;
		}

		& + & {
			margin-left: 90px;

			@include pc {
				margin-left: 90 / 1250 * 100vw;
			}
		
			@include tablet {
				margin: 0 auto 12 / 1024 * 100vw;
			}
		
			@include mobile {
				margin: 0 auto 12 / 360 * 100vmin;
			}
		}
	}

	&__label {
		&__text {
			position: absolute;
			top: 17px;
			font-size: 13px;
			line-height: 14px;
			text-transform: uppercase;
			color: $color-black;
			transition: top, font-size, 0.3s;
			cursor: text;

			@include pc {
				top: 17 / 1250 * 100vw;
				font-size: 13 / 1250 * 100vw;
				line-height: 14 / 1250 * 100vw;
			}
		
			@include tablet {
				top: 17 / 1024 * 100vw;
				font-size: 13 / 1024 * 100vw;
				line-height: 14 / 1024 * 100vw;
			}
		
			@include mobile {
				top: 17 / 360 * 100vmin;
				font-size: 13 / 360 * 100vmin;
				line-height: 14 / 360 * 100vmin;
			}

			.is-active & {
				top: 0;
				font-size: 10px;
				color: rgba($color-black, 0.5);

				@include pc {
					font-size: 10 / 1250 * 100vw;
				}
			
				@include tablet {
					font-size: 10 / 1024 * 100vw;
				}
			
				@include mobile {
					font-size: 10 / 360 * 100vmin;
				}
			}
		}
	}

	&__input {
		outline: none;
		border: none;
		border-radius: 0;
		border-bottom: 3px solid rgba($color-black, 0.2);
		padding: 14px 0 2px;
		width: 100%;
		font-size: 22px;
		line-height: 26px;
		text-transform: none;
		color: $color-black;

		@include pc {
			border-bottom: 3 / 1250 * 100vw solid rgba($color-black, 0.2);
			padding: 14 / 1250 * 100vw 0 2 / 1250 * 100vw;
			font-size: 22 / 1250 * 100vw;
			line-height: 26 / 1250 * 100vw;
		}
	
		@include tablet {
			border-bottom: 3 / 1024 * 100vw solid rgba($color-black, 0.2);
			padding: 14 / 1024 * 100vw 0 2 / 1024 * 100vw;
			font-size: 22 / 1024 * 100vw;
			line-height: 26 / 1024 * 100vw;
		}
	
		@include mobile {
			border-bottom: 2 / 360 * 100vmin solid rgba($color-black, 0.2);
			padding: 14 / 360 * 100vmin 0 2 / 360 * 100vmin;
			font-size: 22 / 360 * 100vmin;
			line-height: 26 / 360 * 100vmin;
		}
	}

	&__submit {
		margin-left: auto;
		outline: none;
		box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.35);
		border-radius: 25px;
		border: none;
		padding: 22px 26px 18px;
		font-weight: 700;
		font-size: 17px;
		line-height: 21px;
		background-color: $color-lemon;
		text-transform: uppercase;
		color: $color-martinique;
		transition: box-shadow 0.15s, background-color 0.3s, color 0.3s;

		@include pc {
			box-shadow: 1 / 1250 * 100vw 1 / 1250 * 100vw 0 rgba(0, 0, 0, 0.35);
			border-radius: 25 / 1250 * 100vw;
			padding: 22 / 1250 * 100vw 26 / 1250 * 100vw 18 / 1250 * 100vw;
			font-size: 17 / 1250 * 100vw;
			line-height: 21 / 1250 * 100vw;
		}
	
		@include tablet {
			margin: 0 auto;
			box-shadow: 1 / 1024 * 100vw 1 / 1024 * 100vw 0 rgba(0, 0, 0, 0.35);
			border-radius: 25 / 1024 * 100vw;
			padding: 22 / 1024 * 100vw 26 / 1024 * 100vw 18 / 1024 * 100vw;
			font-size: 17 / 1024 * 100vw;
			line-height: 21 / 1024 * 100vw;
		}
	
		@include mobile {
			box-shadow: 1 / 360 * 100vmin 1 / 360 * 100vmin 0 rgba(0, 0, 0, 0.35);
			border-radius: 12 / 360 * 100vmin;
			padding: 16 / 360 * 100vmin 20 / 360 * 100vmin 14 / 360 * 100vmin;
			font-size: 14 / 360 * 100vmin;
			line-height: 16 / 360 * 100vmin;
		}

		&:hover {
			box-shadow: 0 0 0 rgba(0, 0, 0, 0.35);
			background-color: $color-martinique;
			color: $color-lemon;
		}
	}


	&.is-alt {
		@include mobile {
			margin: 0;
			background: $color-white;

			.form {
				&__inner {
					padding: 0;
				}
	
				&__desc {
					color: $color-black;
				}
			}
		}
	}
}