.callback {
	display: flex;
	align-items: center;
	flex-shrink: 0;
	justify-content: space-between;
	padding: 11px 23px 9px 21px;
	width: 272px;
	border-radius: 25px;
	background-color: $color-lemon;
	display: block;
	font-weight: 700;
	font-size: 29px;
	line-height: 29px;
	text-transform: uppercase;
	text-decoration: none;
	color: $color-royal-purple-0;

	@include pc {
		font-size: 29 / 1250 * 100vw;
		line-height: 29 / 1250 * 100vw;
	}

	@include tablet {
		font-size: 29 / 1024 * 100vw;
		line-height: 29 / 1024 * 100vw;
	}

	@include mobile {
		font-size: 16 / 360 * 100vmin;
		line-height: 16 / 360 * 100vmin;
	}

	@include pc {
		padding: 11 / 1250 * 100vw 23 / 1250 * 100vw 9 / 1250 * 100vw 21 / 1250 * 100vw;
		width: 272 / 1250 * 100vw;
		border-radius: 25 / 1250 * 100vw;
	}

	@include tablet {
		padding: 11 / 1024 * 100vw 23 / 1024 * 100vw 9 / 1024 * 100vw 21 / 1024 * 100vw;
		width: 272 / 1024 * 100vw;
		border-radius: 25 / 1024 * 100vw;
	}

	@include mobile {
		padding: 6 / 360 * 100vmin 8 / 360 * 100vmin 4 / 360 * 100vmin 10 / 360 * 100vmin;
		width: 145 / 360 * 100vmin;
		border-radius: 12 / 360 * 100vmin;
	}

	&::before {
		content: "";
		display: inline-block;
		margin-right: 5px;
		width: 32px;
		height: 32px;
		background: url("../images/sprites/png/phone@2x.png") 0 0 no-repeat;
		background-size: cover;
		vertical-align: middle;

		@include pc {
			width: 30 / 1250 * 100vw;
			height: 30 / 1250 * 100vw;
		}
	
		@include tablet {
			width: 30 / 1024 * 100vw;
			height: 30 / 1024 * 100vw;
		}
	
		@include mobile {
			width: 18 / 360 * 100vmin;
			height: 18 / 360 * 100vmin;
		}
	}

	&::after {
		content: "ЗВОНКИ БЕСПЛАТНЫЕ";
		display: block;
		margin-left: 37px;
		margin-top: -5px;
		font-weight: 400;
		font-size: 13px;
		line-height: 13px;
		text-transform: uppercase;
		color: $color-royal-purple-0;

		@include pc {
			margin-left: 37 / 1250 * 100vw;
			margin-top: -5 / 1250 * 100vw;
			font-size: 13 / 1250 * 100vw;
			line-height: 13 / 1250 * 100vw;
		}
	
		@include tablet {
			margin-left: 37 / 1024 * 100vw;
			margin-top: -5 / 1024 * 100vw;
			font-size: 13 / 1024 * 100vw;
			line-height: 13 / 1024 * 100vw;
		}
	
		@include mobile {
			margin-left: 22 / 360 * 100vmin;
			margin-top: -2 / 360 * 100vmin;
			font-size: 8 / 360 * 100vmin;
			line-height: 8 / 360 * 100vmin;
		}
	}
}